import React, { useState } from 'react'
import { useSelector } from 'react-redux'

import PropTypes from 'prop-types'

import './DesBlockTable.styles.scss'
import LineBlock from '../../../lib/components/LineBlock'
import RowLineTab from './RowLineTab'
import CreactOrder from '../CreactOrder'
import Button from '../../../lib/components/Button/Button'
/**
 * param type (?id=1232339&type=desadv&mode=add)
 */
const DesBlockTable = React.memo(({ type }) => {
  const { tabSumData, dataOrderTable, dataDesAdvTable } = useSelector(
    (state) => state.desAdv,
  )

  const data = {
    name: 'OrderButton',
    label: 'Создать накладную',
    value: 'regress2506-2',
    type: 'button',
    valueBold: false,
  }

  let curData =
    type === 'order'
      ? dataOrderTable
      : type === 'desadv'
      ? dataDesAdvTable
      : null

  if (!curData) {
    return <div>loading..</div>
  }
  return (
    <>
      <div className="tab-block">
        <table className="tcnt" cellspacing="4" border="1">
          <thead>
            {Object.keys(curData.header).length && (
              <RowLineTab isHeader values={curData.header} />
            )}
          </thead>
          <tbody>
            {curData.data.length &&
              curData.data.map((values, index) => (
                <RowLineTab
                  isHeader={false}
                  key={values.id['value'] + values.EAN['value']}
                  values={values}
                  odd={index}
                />
              ))}
          </tbody>
        </table>
      </div>

      {type === 'order' ? <Button data={data} /> : null}
      <div className="tab-summ">
        {tabSumData.map((line) => (
          <LineBlock data={line} />
        ))}
      </div>
    </>
  )
})

// LineBlock.defaultProps = {
//   name: 'Название(1)',
//   data: {
//     name: 'Номер заказа',
//     value: 'regress2506-2',
//     type: 'div',
//     valueBold: false,
//   },
// }

DesBlockTable.propTypes = {
  type: PropTypes.string,
}

DesBlockTable.defaultProps = {
  type: 'order',
}

export default DesBlockTable
