/**
 * @param values (String)
 * @param isHeader (Bool)
 * @param odd (Number is index)
 * @returns JSX
 */
const RowLineTab = ({ values, isHeader, odd }) => {
  const cls = !isHeader ? (odd % 2 ? 'odds' : 'even') : 't-header'
  console.log('11', values)
  // debugger
  return isHeader ? (
    <tr className={cls}>
      {Object.entries(values).map(([key, value]) => (
        <ThRender key={key + odd} row={value} />
      ))}
    </tr>
  ) : (
    <tr className={cls}>
      {Object.entries(values).map(([key, value]) => (
        <TdCell key={key + odd} name={key} row={value} id={odd} />
      ))}
    </tr>
  )
}

RowLineTab.propTypes = {
  name: PropTypes.string,
  values: PropTypes.object,
  isHeader: PropTypes.bool,
  odd: PropTypes.bool,
}

