/**
 *
 * @param {row} {value: '4053158943602', type: 'string', style: {…}}
 * @param {id} 0 || 1 || 2
 * @returns
 */
function TdCell({ name, row, id }) {
  const [searchParam, setSearchParam] = useSearchParams()

  const idDoc = searchParam.get('id')
  const typeDoc = searchParam.get('type')
  const divOnChange = (e) => {
    // const target = e.target.dataset.type
    // const value = e.target.innerText
    // console.log('target', target, value)
  }
  // debugger
  if (!row) {
    return null
  }

  if (row['style'] && row['style']['readonly']) {
    return (
      <td key={row} className={'row-block__body'} data-type={name}>
        {row['value']}
      </td>
    )
  }
  return (
    <td key={row} className={'row-block__body'} data-type={name}>
      {name === 'Vat' ? (
        <SelectRender id={id} row={row['value']} mode={typeDoc} />
      ) : name === 'QTY' || name === 'Price1' ? (
        <InputRender id={id} row={row['value']} name={name} />
      ) : (
        <div
          className="editing-div"
          data-type={name}
          contentEditable
          onInput={divOnChange}
        >
          {row['value']}
        </div>
      )}
    </td>
  )
}

function SelectRender({ row, id, mode }) {
  const [rowValue, setRowValue] = useState(row)
  // const editMode = useSelector((state) => state.desAdv.isEdit)

  const dispatch = useDispatch()

  const setRow = (e) => {
    const value = e.target.value
    setRowValue(value)
    // console.log(id)
    // console.log({ vat })
    // if (id === 2 || id === 0) {
    let newData = { id, value }
    // dispatch(switchQty({ newData }))
    dispatch(switchTabLineSelect({ newData }))
    // }
  }

  React.useEffect(() => {
    if (rowValue === row) {
      return
    }
    let value = row
    let newData = { id, value }
    console.log('newData', newData)
    setRowValue(row)

    dispatch(switchTabLineSelect({ newData }))
  }, [row])

  return mode === 'order' ? (
    <div>{row}</div>
  ) : (
    <select value={rowValue} onChange={setRow}>
      <option value="0">0</option>
      <option value="10">10</option>
      <option value="18">18</option>
      <option value="20">20</option>
    </select>
  )
}

function InputRender({ id, name, row, mode }) {
  const [rowValue, setRowValue] = useState(row)
  const [errorValue, setErrorValue] = useState(false)
  const dispatch = useDispatch()
  const { dataDesAdvTable } = useSelector((state) => state.desAdv)

  const refCurChar = useRef(0)
  // const editMode = useSelector((state) => state.desAdv.isEdit)
  // console.log('header', header)

  const validateError = (value) => {
    if (
      (!validateQty(name, value) && name === 'QTY') ||
      (!validatePrice1(name, value) && name === 'Price1')
    ) {
      setErrorValue(true)
    } else {
      setErrorValue(false)
    }
  }

  React.useEffect(() => {
    validateError(row)
  }, [row])

  const setRow = (e) => {
    let value = e.target.value
    if (!refCurChar.current) {
      return false
    }
    console.log('value123', value)
    // if (!chartValid.includes(value)) {
    //   return 0
    // }
    if (value === ',') {
      value = '.'
    }

    // console.log(id, header, value)
    // let newValue = value
    if (value.includes('.')) {
      // if (value.split('.')[1].length > 2 && header === 'Price1') {
      //   return
      // }
      // const newData = { id, header, value }

      if (!validateError(value)) {
        setRowValue(value)
      }
    } else {
      //let extendNum = header === 'QTY' ? '.000' : '.00'
      const nv = value //? value + extendNum : '1' + extendNum
      setRowValue(nv)
      validateError(nv)
      // const newData = { id, header, nv }
    }
    refCurChar.current = false
  }

  const setRowBlur = (e) => {
    let value = e.target.value
    let newData = { id, name, value }
    console.log('setRowBlur', newData)
    if (name === 'QTY') {
      dispatch(switchQty({ newData }))
      value = '-1' // for update line
      // newData = { id, name, value }
      const rowData = helperSetValue({ id, name, value, dataDesAdvTable })
      newData = { id, name, rowData }
      dispatch(setPrice({ newData }))
    } else if (name === 'Price1') {
      const rowData = helperSetValue({ id, name, value, dataDesAdvTable })
      // console.log('44', rowData)
      newData = { id, name, rowData }
      dispatch(setPrice({ newData }))
    }
  }

  const onKeyDownHandler = (e) => {
    // console.log(e.key)
    if (chartValid.includes(e.key)) {
      refCurChar.current = true
      setErrorValue(true)
      return false
    }
  }
  return (
    <input
      style={{
        border: errorValue && !mode ? '1px solid red' : '1px solid transparent',
      }}
      disabled={mode}
      value={rowValue}
      onChange={setRow}
      onKeyDown={onKeyDownHandler}
      onBlur={setRowBlur}
    />
  )
}

export default RowLineTab
