export function helperSetValue({ id, name, value, dataDesAdvTable }) {
  /**
   * Price1 in tabLine
   * Price2 change (Цена с НДС)
   * NetAmount change (Сумма без НДС)
   * TaxAmount change
   * GrossAmount change (Сумма с НДС)
   *
   * В общей таблице
   * Сумма без НДС всего NetAmount
   * Сумма с НДС всего Vat
   * НДС всего Vat
   */

  //if (header === 'QTY')
  if (value === '-1') {
    value = dataDesAdvTable.data[id].Price1['value']
  }
  // debugger
  const price1 = parseFloat(value) //6.00
  // console.log({ price1 })
  /**
   * count Price2 = vat(НДС) + price1
   */
  const vat = parseInt(dataDesAdvTable.data[id].Vat['value'])
  let price2 = (vat * price1) / 100
  price2 += price1

  /**
   * count NetAmount = Qty(количество) * price1
   */
  const qty = parseInt(dataDesAdvTable.data[id].QTY['value'])
  const netAmount = qty * price1

  /**
   * TaxAmount
   */
  let taxAmount = (netAmount / 100) * vat

  /**
   * GrossAmount = netAmount + netAmount/100 * Vat(НДС)
   */

  let grossAmount = (netAmount / 100) * vat
  grossAmount += netAmount
  /**
   * Сумма без НДС всего(TabSumm) = netAmount все из выборки
   */

  let netAmountAll = netAmount
  dataDesAdvTable.data.forEach((el, index) => {
    if (index !== id) {
      netAmountAll += parseFloat(el.NetAmount['value'])
    }
  })

  /**
   * Сумма  НДС всего = taxAmount все из выборки
   */

  let taxAmountAll = taxAmount
  dataDesAdvTable.data.forEach((el, index) => {
    if (index !== id) {
      taxAmountAll += parseFloat(el.TaxAmount['value'])
    }
  })
  /**
   * Сумма c НДС всего = grossAmount все из выборки
   */
  let grossAmountAll = grossAmount
  dataDesAdvTable.data.forEach((el, index) => {
    if (index !== id) {
      grossAmountAll += parseFloat(el.GrossAmount['value'])
    }
  })

  const newPrice1 = {
    ...dataDesAdvTable.data[id].Price1,
    value: formatData(price1),
  }
  console.log(newPrice1)
  const newPrice2 = {
    ...dataDesAdvTable.data[id].Price2,
    value: formatData(price2),
  }
  const newNetAmount = {
    ...dataDesAdvTable.data[id].NetAmount,
    value: formatData(netAmount),
  }
  const newGrossAmount = {
    ...dataDesAdvTable.data[id].GrossAmount,
    value: formatData(grossAmount),
  }
  const newTaxAmount = {
    ...dataDesAdvTable.data[id].TaxAmount,
    value: formatData(taxAmount),
  }
  const newData = {
    ...dataDesAdvTable.data[id],
  }
  newData['Price1'] = newPrice1
  newData['Price2'] = newPrice2
  newData['NetAmount'] = newNetAmount
  newData['GrossAmount'] = newGrossAmount
  newData['TaxAmount'] = newTaxAmount

  delete newData.style
  delete newData.value
  delete newData.type
  return newData
}

export function formatData(arg) {
  if (typeof arg === 'number') {
    return arg.toFixed(2)
  } else if (typeof arg === 'string') {
    if (arg.includes('.')) {
      let tmpArr = arg.split('.')
      return tmpArr[0] + '.' + tmpArr[1].slice(2)
    } else {
      return arg + '.00'
    }
  }
}

/**
 *
 * @param {string} vat
 * @param {string} price1
 * @returns
 */
export function getPrice2(vat, price1) {
  const nds = (parseFloat(vat) * parseFloat(price1)) / 100
  return (parseFloat(price1) + nds).toFixed(2)
}
export function validatePrice1(header, row) {
  var pattern = /^\d+.\d\d$/
  // var pattern = /\w/gi
  return pattern.test(row) && row.includes('.') // returns a boolean
}

export function validateQty(header, row) {
  let isNullOfCent = true
  let row2 = row
  // console.log(row)
  if (typeof row2 === 'string' && row2.includes('.')) {
    let num2 = row2.split('.')[1]
    isNullOfCent = num2 === '000'
  }
  var pattern = /^\d+.\d\d\d$/
  return header === 'QTY' && row <= 55 && pattern.test(row2) && isNullOfCent
}
